body {
    background: url(./rivendell.jpg);
    background-position: center;
}

@media only screen and (max-width: 500px) {
    .container {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 5vh 40vh 10vh 40vh 5vh;
        height: 100vh;
        justify-items: center;
        align-items: center;
        overflow: hidden;
    }

    .quote {
        width: 22rem;
        height: 18rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 3px solid white;
        background-color: rgba(100,100,100,0.5);
        text-align: center;
        padding: 0.5rem;
        color: white;
        opacity: 0.8;
        transform: scale(0.8);
        overflow: scroll;
    }

    .quote::-webkit-scrollbar {
        display: none;
    }

    .options {
        font-weight: 500;
        width: 22rem;
        height: 18rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        border: 3px solid white;
        background-color: rgba(100,100,100,0.5);
        text-align: center;
        padding: 0.5rem;
        color: white;
        opacity: 0.8;
        transform: scale(0.8);
    }
    .makeOptionsTransparent {
        font-weight: 500;
        width: 22rem;
        height: 18rem;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 3px solid white;
        background-color: rgba(100,100,100,0.5);
        text-align: center;
        padding: 0.5rem;
        color: rgba(0,0,0,0);
        opacity: 0.8;
        transform: scale(0.8);
    }
}

