.quoteText {
    font-weight: 400;
    font-size: 20px;
    margin: auto;
}

.score {
    color: white;
    opacity: 0.8;
}

.resultText {
    margin: 0;
    margin-bottom: 0.5rem;
    font-size: 18px;
}

.answer {
    /*color: rgb(0,200,0);*/
}

.individualOptions {
    display: flex;
    justify-content: flex-end;
    padding: 0.2rem;
    margin: 0;
}

button {
    margin-top: 2rem;
    padding: 1rem;
    border-radius: 10px;
    color: white;
    background-color: rgba(0,0,0,0.8);
    border: none;
}

button:focus {
    outline: none;
}

button:hover {
    color: black;
    background-color: rgba(255,255,255,0.8);
}

input[type=radio] {
    width: 1.3rem;
    height: 1.3rem;
    margin-left: 0.5rem;
}
